.place {
  &__photo-container {
    position: relative;

    padding-bottom: 0;
    box-sizing: border-box;
    width: 100%;
    max-height: 256px;

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    

      @include onDesktop {
        height: 256px;
        padding-bottom: 0;
      }
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 8px;

    object-fit: cover;
    object-position: center;
    transition: transform 0.5s;

    &:hover {
      transform: scale(105%);
    }
  
  }

  &__photos {
    margin-bottom: 16px;

    @include onDesktop {
      margin-bottom: 24px;
    }
  }

  &__link {
    font-weight: 700;
    text-decoration: none;
    color: $colorBlue;
  }
}
